import { useState } from "react";
import { useMutation } from "@apollo/client";
import toast from "react-hot-toast";
import { Link, useHistory } from "react-router-dom";

import Loader from "../../utils/Loader";
import { REGISTER } from "../../gql/mutations/register";

function Register() {
  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [username, setUsername] = useState();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [confirmPassword, setConfirmPassword] = useState();

  const history = useHistory();

  const [register, { loading }] = useMutation(REGISTER, {
    onCompleted: (data) => {
      toast.success(" ");
      history.push("/login");
    },
    onError: (error) => {
      console.log(error);
      toast.error(
        error.message == "UNIQUE constraint failed: main_extendeduser.username"
          ? "    "
          : error.message ==
            "UNIQUE constraint failed: main_extendeduser.email"
          ? " email  "
          : " "
      );
    },
  });

  function registrationHandler(e) {
    e.preventDefault();
  }
  return (
    <>
      <Loader loading={loading} />
      <div className="container login__container">
        <div>
          <h1>
            
            <br />
            !
          </h1>

          <form
            onSubmit={(e) => {
              registrationHandler(e);
              if (password !== confirmPassword) {
                toast.error("  ");
                return;
              }
              register({
                variables: {
                  firstName,
                  lastName,
                  username,
                  password,
                  email,
                },
              });
            }}
          >
            <input
              className="default__input"
              type="text"
              placeholder=""
              defaultValue={firstName}
              onChange={(e) => setFirstName(e.target.value)}
              required
            />
            <input
              className="default__input"
              type="text"
              placeholder=""
              defaultValue={lastName}
              onChange={(e) => setLastName(e.target.value)}
              required
            />
            <input
              className="default__input"
              type="text"
              placeholder=""
              defaultValue={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
            <input
              className="default__input"
              type="text"
              placeholder="Email"
              defaultValue={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            <input
              className="default__input"
              type="password"
              placeholder=""
              defaultValue={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <input
              className="default__input"
              type="password"
              placeholder=" "
              defaultValue={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />

            <div className="d-flex">
              <span>
                   -{" "}
                <Link className="default__link" to={"/login"}>
                  
                </Link>
              </span>
            </div>
            <button type="submit" className="default__btn mt-2">
              
            </button>
          </form>
        </div>
      </div>
    </>
  );
}

export default Register;
